.loader {
  display: inline-flex;
  align-items: center;
  gap: 8px;

  padding: 16px;
}

.loader-text {
  font-size: 16px;
}

.icon-spin {
  margin-right: 16px;
  animation: icon-spin 2000ms infinite linear;
}

@keyframes icon-spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(359deg);
  }
}
